
import {Component, OnInit, ElementRef, ViewChild, Inject, Injectable} from '@angular/core';
import {SaveTreasureService} from "../save-treasure.service";
import { WindowRef } from './windowRef';
import { TextToSpeech } from '@ionic-native/text-to-speech/ngx';

@Component({
    selector: 'app-home',
    templateUrl: 'home.page.html',
    styleUrls: ['home.page.scss'],
})
export class HomePage implements OnInit {
    @ViewChild('canvas', {static: true})
    canvasMain: ElementRef<HTMLCanvasElement>;
    @ViewChild('dot', {static: true})
    canvasDot: ElementRef<HTMLCanvasElement>;
    ctxMap: any;
    ctxDot: any;
    cordCurrentLong: number;
    cordCurrenttLat: number;
    currentMoveArray = [0, 0];
    startCoords = [330, 250];
    coords = [330, 250];
    step = 10;
    img = new Image();
    location: string;
    alertIsShow = false;
    window: Window;
    beta: number;
    gamma: number;
    speedX = 0;
    speedY = 0;
    isStarted = false;
    isFinished = false;
    audioMuted = false;
    bgImage = 'assets/audioPlay.png';
    

    constructor(public windowRef: WindowRef, public treasureService: SaveTreasureService, private tts: TextToSpeech) {
        this.window = windowRef.nativeWindow;
    }
    

    ngOnInit() {
        this.ctxMap = this.canvasMain.nativeElement.getContext('2d');
        this.ctxDot = this.canvasDot.nativeElement.getContext('2d');

        this.img.src = 'assets/map2.png';
        this.img.onload = () => {
            this.ctxMap.drawImage(this.img, 0, 0);
            this.drawDot(this.coords);
        };

        setInterval(() => {
            let x = this.coords[0] + this.speedX;
            let y = this.coords[1] + this.speedY;
            var imgData = this.ctxMap.getImageData(x, y, 1, 1).data;
            var R = imgData[0];
            if (R < 200) {
                return false;
            }

            this.coords[0] = this.coords[0] + this.speedX;
            this.coords[1] = this.coords[1] + this.speedY;
            this.drawDot(this.coords);
            this.treasureService.setTreasureDistance(this.coords);
        }, 40)


        document.onkeyup = e => {
            this.ctxDot.fillStyle = "#ff2626";
            var newCoors = [...this.coords];
            var itIsArrow = false;
            switch (e.keyCode) {
                // left
                case 37:
                    newCoors[0] -= this.step;
                    itIsArrow = true;
                    break;
                //up
                case 38:
                    newCoors[1] -= this.step;
                    itIsArrow = true;
                    break;

                // right
                case 39:
                    newCoors[0] += this.step;
                    itIsArrow = true;
                    break;

                //down
                case 40:
                    newCoors[1] += this.step;
                    itIsArrow = true;
                    break;
            }

            if (itIsArrow) {
                var imgData = this.ctxMap.getImageData(newCoors[0], newCoors[1], 1, 1).data;
                console.log(imgData);
                var R = imgData[0];
                if (R < 200) {
                    this.showAlert();
                    return false;
                }

                this.coords = [...newCoors];
                this.drawDot(this.coords);
                this.treasureService.setTreasureDistance(this.coords);
            }

            e.stopPropagation();
            e.preventDefault();
        }
    }

    audioToggle(){
        if(this.audioMuted === false) {
            this.audioMuted = true;
            this.bgImage = 'assets/audioStop.png';
            console.log(this.audioMuted);
        }
        else {
            this.audioMuted = false;
            this.bgImage = 'assets/audioPlay.png';
            console.log(this.audioMuted);
        }

    }
    showAlert() {
        if (this.alertIsShow === true) return;
        this.alertIsShow = true;
        setTimeout(() => {
            this.alertIsShow = false;
        }, 200);
    }

    drawDot(coords) {
        this.ctxDot.clearRect(0, 0, 1000, 1000);
        this.ctxDot.beginPath();
        this.ctxDot.fillStyle = "#ff2626";
        this.ctxDot.arc(coords[0], coords[1], 3, 0, Math.PI * 2, true);
        this.ctxDot.fill();
    }

    resetDot() {
        this.coords = [330, 250];
        this.drawDot([330, 250]);
        this.currentMoveArray = [0, 0];
        
        this.treasureService.setTreasureDistance(this.coords);
    }

    startAgain(){
        this.isFinished = false;
        this.tts.speak('Hide the treasure');
    }
    getOrientation(){
        this.tts.speak('Hide the treasure');
        var audio1 = document.getElementById('music');
        //@ts-ignore
        audio1.volume = 0.2;
        //@ts-ignore
        audio1.play();
        
        let _this = this;
        //@ts-ignore
        if (typeof(DeviceOrientationEvent) !== 'undefined' && typeof(DeviceOrientationEvent.requestPermission) === 'function'){
            //@ts-ignore
            DeviceOrientationEvent.requestPermission()
            .then(response => {
                if (response == 'granted') {
                    window.addEventListener('deviceorientation', (e) => {
                            _this.isStarted = true;
                            window.addEventListener('deviceorientation', (e) => {
                            this.beta = e.beta;
                            this.gamma = e.gamma;
                            this.speedX = e.gamma / 3;
                            this.speedY = e.beta / 3;
        })

                    })
                }
            })
            .catch(console.error)
    }else {
            _this.isStarted = true;
        window.addEventListener('deviceorientation', (e) => {
            this.beta = e.beta;
            this.gamma = e.gamma;
            this.speedX = e.gamma / 3;
            this.speedY = e.beta / 3;
        })
        }
    }

    dig() {
        if (this.treasureService.treasureIsBuried) return false;
        
        this.treasureService.setTreasureBuried(true);
        this.treasureService.setTeasureCoords(this.coords);
        this.treasureService.setTreasureDistance(this.coords);
        this.resetDot();
        this.tts.speak('Find the treasure');
    }

    digIt() {
        if (this.treasureService.distance >= 15) {
          this.tts.speak("TRY AGAIN");
        } else {
            this.treasureService.setTreasureBuried(false);
            this.resetDot();
            this.isFinished = true;
            this.tts.speak('WOW, YOU ARE SO GOOD');
        }
    }

}
